import React, { Component } from 'react';
import axios from 'axios';
import './App.css';

class ShowRoom extends Component {
  constructor(props) {
    super(props)
    this.state = {
      id : props.id,
      vehicle : {},
    };
    this.fetch = this.fetch.bind(this);
  }

  componentDidMount() {
  }

  fetch(){
    axios.get(`https://private-4e19e-interviewapi3.apiary-mock.com/vehicles/` + this.state.id)
      .then(data => {
        let v = data.data.data.vehicles;
        this.setState({vehicles : v});
        console.log(v)
      })
      .catch(res => {
        if(res instanceof Error) {
          console.log("Error is: ", res.message);
        } else {
          console.log("Other error: ", res.data);
        }
      });
  }

  handleclick(){
    // need to send up to favorites
    return "checkmark";
  }

  render() {
    return (
        <div id='showroomsub'>
            No Car is selected
        </div>
    );
  }
}

export default ShowRoom;
